html {
  text-transform: uppercase;
  --header-height: 60px;
  --hover-color: #00acdc;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  margin: 0;
}

.header {
  height: var(--header-height);
  justify-content: space-between;
  align-items: center;
  padding: 0;
  display: flex;
}

.logo {
  margin-left: 50px;
}

.nav {
  height: var(--header-height);
  align-items: center;
  margin-right: 50px;
  display: flex;
}

.nav__item {
  height: 100%;
  padding-left: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  height: 100%;
  display: block;
}

.nav__item > .nav__link:not(:last-child) {
  margin-right: 20px;
}

.nav__a {
  line-height: var(--header-height);
  height: 100%;
  display: block;
  position: relative;
}

.is-active {
  color: var(--hover-color);
}

.is-active:after {
  content: "";
  background-color: var(--hover-color);
  border-radius: 4px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.nav__a:hover {
  color: var(--hover-color);
}
